// @ts-check
const R = require('ramda')

/**
 *field'key'
 *list  key
 * @param {array} list
 * @param {any} key
 * @return {key:xxx,value:yyy}
 */
export const getMatchedValue = R.curry(
  (list, key, field = 'key') => R.find(R.propEq(field, key))(list) || {}
)

/**
 * itemkeyvalues
 *
 * @param {any} key
 * @param {any} values
 * @param {any} item
 */
const isContained = R.curry((key, values, item) =>
  R.contains(item[key], values)
)

/**
 *  list keyvalues
 *
 * @param {any} list
 * @param {any} key
 * @param {any} values
 */
export const getContainsValus = (list, key, values) =>
  R.filter(isContained(key, values), list)

/**
 *Cascader 
 *
 * @param {any} arr
 * @param {any} labelName
 * @param {any} valueName
 */
export const enumTransform = (arr, labelName = 'value', valueName = 'key') =>
  arr.map(item => {
    var obj = {
      label: item[labelName],
      value: item[valueName]
    }
    if (item.children) {
      obj.children = enumTransform(item.children, labelName, valueName)
    }
    return obj
  })

/**
 * Cascader key  
 *
 * @param {any} arr
 * @param {any} values
 * @param {any} labelName
 * @returns
 */
export const getEnumLabels = (arr, values, labelName) => {
  if (!arr || arr.length === 0) {
    return undefined
  }
  var result = []
  var arrs = arr
  values.forEach(v => {
    const obj = getMatchedValue(arrs, v, labelName)
    result.push(obj.label)
    arrs = obj.children
  })
  return result
}

/**
 * Cascader,
 *
 * @param {any} arr
 * @param {any} obj
 */
export const unboxSingleCascader = (arr, obj) => {
  arr.forEach(key => {
    obj[key] = obj[key][0]
  })
  return obj
}



